body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  background-color: #fffafa;
  width: 100%;
}

body, #root {
  width: 100%;
}

#logo-container {
  display: -webkit-flex;
  display: flex;
  height: 130px;
}

#logo {
  width: 12%;
  margin: auto;
  
}

.app-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.menu-content {
  display: -webkit-flex;
  display: flex;
}

.app-content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  width: 100%;
  padding: 20px;
  box-sizing: border-box;
}

.menu-options {
  display: inline-block;
  margin: auto;
}


p {
  /* display: inline-block;
  margin: 40px auto; */
  display: -webkit-flex;
  display: flex;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
}


.current-client-name {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 10px auto;
  font-family: 'Courier New', Courier, monospace;
  font-size: 20px;
}

table {
  text-align: center;
}
.food-menu {
    display: inline-block;
    margin: 10px auto;
    font-size: 20px;
    font-family: 'Courier New', Courier, monospace;
    border: none;
    padding: 10px;
    background-color: #ffc0d0;
    border-radius: 10px;
}

.food-menu:focus {
    outline: none;
} 

.trash-can {
    color: #ff5d9e;
    cursor: pointer;
    /* color: purple; */
}
.container-garzones {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 100%;
}

.container-opciones-menu, .container-current-order {
    width: 50%;
    display: -webkit-flex;
    display: flex;
    padding: 20px;
    -webkit-flex-direction: column;
            flex-direction: column;
}

#add-customer-button {
    display: block;
    margin: 10px auto;
    padding: 20px;
    font-family: 'Courier New', Courier, monospace;
    background-color: #ffc0d0;
    border-radius: 10px;
    border: none;
    font-size: 20px;
}

#add-customer-button:focus {
    outline: none;
    border: solid 2px #ff5d9e;
    border-radius: 10px;
}

#customer-name {
    margin: 10px auto;
    font-family: 'Courier New', Courier, monospace;
    font-size: 20px;
}

#customer-name:focus {
    outline: 2px solid #ffe0e0
}

.circulo-pedidos-listos {
    position: fixed;
    top: 10px;
    left: 10px;
    display: -webkit-flex;
    display: flex;
    margin: 10px auto;
    font-size: 20px;
    font-family: 'Courier New', Courier, monospace;
    border: none;
    padding: 10px;
    background-color: #ffc0d0;
    border-radius: 100%;
    height: 100px;
    width: 100px;
    word-break: normal;  
    cursor: pointer;
}

.circulo-pedidos-listos p {
    display: block;
}

.container-pedidos {
    width: 50%;
    height: 100%;
    background-color: #ffc0d0;
    position: fixed;
    top: 0;
    right: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    overflow-y: scroll;
}

.table-card2 {
    width: 90%;
    background-color: #ffe0e0;
    margin: 10px auto;
    border-radius: 10px;
}


.menu-button {
    display: block;
    margin: 10px auto;
    background-color: #ffe0e0;
    border: none;
    font-family: 'Courier New', Courier, monospace;
    font-size: 30px;
    border-radius: 10px;
  }
  
  .menu-button-selected {
    display: block;
    margin: 10px auto;
    background-color: #ffe0e0;
    border: 3px solid #ffc0d0;
    font-family: 'Courier New', Courier, monospace;
    font-size: 30px;
    border-radius: 10px;
  }
  
  .menu-button:focus {
    outline: none;
} 

.menu-button-selected:focus {
  outline: none;
}
.table-card {
    background: #ffe0e0;
    margin: 10px auto;
    border: 1px solid #ffc0d0;
    padding: 10px;
    padding-top: 5px;
    border-radius: 2%;
    min-width: 40%;
    max-width: 40%;
    font-family: 'Courier New', Courier, monospace;
    font-size: 20px;
    box-sizing: content-box;
    word-break: break-all;
    text-align: start;
}

.table-col {
    padding: 5px;
}

.index {
    word-break: keep-all;
    font-size: 30px;
    text-align: center;
}

.order-number {
    background: #ffc0d0;
    color: #fffafa;
    height: 50px;
    width: 50px;
    border-radius: 100%;
    padding: 10px;
    position: relative;
    left: -40px;
    top: -30px;
}

.kitchen-customer-name {
    font-size: 40px;
}

.btn-ready {
    display: inline-block;
    margin: 10px auto;
    font-size: 20px;
    font-family: 'Courier New', Courier, monospace;
    border: none;
    padding: 10px;
    background-color: #ffc0d0;
    border-radius: 10px;
    cursor: pointer;
}

.btn-ready {
    outline: none;
}

.countdown {
    background-color: #fffafa;
    padding: 5px;
    font-weight: 600;
}
